<template>
  <div class="main-div">
    <div class="main-bg">
      <DialogueTheme class="theme" :msgs="message_7" :speaker="speaker" :finishConversation="finishConversation_7" v-if="showDialogue_7" />
      <Selection id="selection" :items="selectionItems_7" v-if="showSelection_7" :finishSelection="finishSelection_7" />
      <DialogueTheme class="theme" :msgs="message_7_1" :speaker="speaker" :finishConversation="finishConversation_7_1" v-if="showDialogue_7_1" />
      <DialogueTheme class="theme" :msgs="message_7_2" :speaker="speaker" :finishConversation="finishConversation_7_2" v-if="showDialogue_7_2" />
      <DialogueTheme class="theme" :msgs="message_7_3" :speaker="speaker" :finishConversation="finishConversation_7_3" v-if="showDialogue_7_3" />
      <Selection id="selection" :items="selectionItems_8" v-if="showSelection_8" :finishSelection="finishSelection_8" />
      <DialogueTheme class="theme" :msgs="message_9" :speaker="speaker" :finishConversation="finishConversation_9" v-if="showDialogue_9" />
      <Selection id="selection" :items="selectionItems_9" v-if="showSelection_9" :finishSelection="finishSelection_9" />
      <DialogueTheme class="theme" :msgs="message_10" :speaker="speaker" :finishConversation="finishConversation_10" v-if="showDialogue_10" />
      <Selection id="selection" :items="selectionItems_10" v-if="showSelection_10" :finishSelection="finishSelection_10" />
      <DialogueTheme class="theme" :msgs="message_11" :speaker="speaker" :finishConversation="finishConversation_11" v-if="showDialogue_11" />
      <Selection id="selection" :items="selectionItems_11" v-if="showSelection_11" :finishSelection="finishSelection_11" />
      <DialogueTheme class="theme" :msgs="message_11_1" :speaker="speaker" :finishConversation="finishConversation_11_1" v-if="showDialogue_11_1" />
      <DialogueTheme class="theme" :msgs="message_11_2" :speaker="speaker" :finishConversation="finishConversation_11_2" v-if="showDialogue_11_2" />
      <DialogueTheme class="theme" :msgs="message_12" :speaker="speaker" :finishConversation="finishConversation_12" v-if="showDialogue_12" />
      <Selection id="selection" :items="selectionItems_13" v-if="showSelection_13" :finishSelection="finishSelection_13" />
      <DialogueTheme class="theme" :msgs="message_13_1" :speaker="speaker" :finishConversation="finishConversation_13_1" v-if="showDialogue_13_1" />
      <DialogueTheme class="theme" :msgs="message_13_2" :speaker="speaker" :finishConversation="finishConversation_13_2" v-if="showDialogue_13_2" />
      <div id='avator'></div>
    </div>
  </div>
</template>

<script>
import DialogueTheme from '@/components/DialogueTheme'
import Selection from '@/components/Selection'

export default {
  name: 'CommuteSouthMainStation',
  components: {
    DialogueTheme,
    Selection
  },
  data () {
    return {
      speaker: '阿  勤',
      showDialogue_7: true,
      message_7: ['終點站，怪奇站—到了，感謝您的搭乘，並請各位旅客注意您隨身的行李⋯⋯嗯？客人您還好嗎？我們已經到終點站了，請趕快下車。'],
      showSelection_7: false,
      selectionItems_7: this.hasPressSleepOver ? ['載我到怪奇博物館，否則就對你不客氣啦。',
        '我是怪奇博物館的新館員，館長要我帶你回去。'] : ['載我到怪奇博物館，否則就對你不客氣啦。',
        '我是怪奇博物館的新館員，館長要我帶你回去。',
        '睡過頭了⋯⋯抱歉，我馬上下車。'],
      showDialogue_7_1: false,
      message_7_1: ['唉，這不是逼我加班嗎？那可得要有加班費才行呀。客人，不如就用⋯⋯你的命來支付吧（笑）'],
      showDialogue_7_2: false,
      message_7_2: ['老闆還要我回去？唉⋯⋯你明白每天這樣通勤上下班，有多累人嗎？我不能休息一下嗎？'],
      showDialogue_7_3: false,
      message_7_3: ['……就這樣回去啦？好吧，掰掰。'],
      showSelection_8: false,
      selectionItems_8: ['振作點吧，工作就是這樣。',
        '是挺累人的呢，其實我也有點累。'],
      showDialogue_9: false,
      message_9: ['啊，不好意思忘了自我介紹。我是９萬名基隆人每天不得不通勤到台北上班而產生的空虛感，累積形成的怪奇，你⋯⋯叫我「阿勤」好了，呵呵。', '欸，你的工作是到處捕捉怪奇對吧？不會覺得很沒有意義嗎？每天從這個城市，移動到那個城市；工作開始就期待著結束，結束了很快又不得不再開始。'],
      showSelection_9: false,
      selectionItems_9: ['社會就是這樣運作的，不能適應的話也太沒用了吧？', '我也有同感呢，為什麼假日都那麼短？好想週休五日啊⋯⋯'],
      showDialogue_10: false,
      message_10: ['你知道最好笑的是什麼嗎？我們以「青春」為車資，忍受著每日的通勤，希望能到達名為「夢想」的終點站；', '但原來我們不是乘客，最後我們都成了「現實」這台車的零件，不停地運轉著、運轉著⋯⋯'],
      showSelection_10: false,
      selectionItems_10: ['哈哈哈，好笑、好笑，你真幽默欸！', '與其說是笑話，我覺得這是恐怖故事吧⋯⋯'],
      showDialogue_11: false,
      message_11: ['有意思，你的反應似乎跟其他人不太一樣呢，大部分我遇到的館員，都只想趕快收服我回去交差，但你似乎⋯⋯真的有聽懂我在說什麼呢。', '說實話，我沒有什麼不能回去博物館的理由，但我也不想那麼聽話。我有個願望，如果你能協助我完成，我就跟你回去，如何？'],
      showSelection_11: false,
      selectionItems_11: ['太麻煩了，我們還是戰鬥吧。', '願望？說來聽聽。'],
      showDialogue_11_1: false,
      message_11_1: ['嘖，你果然還是個無趣的傢伙。就讓你也被空虛感吞噬吧！'],
      showDialogue_11_2: false,
      message_11_2: ['說簡單挺簡單，說麻煩也是呢，是這樣的，我總是為了某個「目的」而移動；卻不曾為了旅行而旅行。所以，我想要漫無目的的搭一次火車⋯⋯', '你那什麼表情？怪奇也是可以搭火車的呀。總之，你陪我搭一趟火車到「三坑站」，我就跟你回博物館，就這麼簡單。'],
      showDialogue_12: false,
      message_12: ['跟你說話真沒意思，我們還是趕快打一打分個輸贏吧。'],
      showSelection_13: false,
      selectionItems_13: ['太麻煩了，我們還是戰鬥吧。', '好，我們走吧。（需花費車資15元。)'],
      showDialogue_13_1: false,
      message_13_1: ['嘖，你果然還是個無趣的傢伙。就讓你也被空虛感吞噬吧！'],
      showDialogue_13_2: false,
      message_13_2: ['不知道會不會很無聊呢⋯⋯但不管了，我就是想這麼做，哈哈。']
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', true)
  },
  computed: {
    hasPressSleepOver () {
      return this.$store.state.hasPressSleepOver
    }
  },
  methods: {
    currruntImpression () {
      console.log('好感' + this.$store.state.goodImpression)
      console.log('惡感' + this.$store.state.badImpression)
    },
    finishConversation_7 (msgs) {
      this.showDialogue_7 = false
      this.showSelection_7 = true
    },
    finishSelection_7 (index) {
      this.showSelection_7 = false
      console.log(index)
      switch (index) {
        case 0:
          this.showDialogue_7_1 = true
          break
        case 1:
          this.showDialogue_7_2 = true
          break
        case 2:
          this.showDialogue_7_3 = true
          break
        default:
          break
      }
    },
    finishConversation_7_1 (msgs) {
      this.showDialogue_7_1 = false
      this.$router.push({
        path: '/commute-kim-hauntedhouse-fight'
      })
    },
    finishConversation_7_2 (msgs) {
      this.showDialogue_7_2 = false
      this.showSelection_8 = true
    },
    finishConversation_7_3 (msgs) {
      this.showDialogue_7_3 = false
      this.$store.commit('pressHasSleepOver')
      window.location.reload()
    },
    finishSelection_8 (index) {
      this.showSelection_8 = false
      console.log(index)
      switch (index) {
        case 0:
          this.$store.commit('addBadImpression')
          break
        case 1:
          this.$store.commit('addGoodImpression')
          break
        default:
          break
      }
      this.currruntImpression()
      this.showDialogue_9 = true
    },
    finishConversation_9 (msgs) {
      this.showDialogue_9 = false
      this.showSelection_9 = true
    },
    finishSelection_9 (index) {
      this.showSelection_9 = false
      console.log(index)
      switch (index) {
        case 0:
          this.$store.commit('addBadImpression')
          break
        case 1:
          this.$store.commit('addGoodImpression')
          break
        default:
          break
      }
      this.currruntImpression()
      this.showDialogue_10 = true
    },
    finishConversation_10 (msgs) {
      this.showDialogue_10 = false
      this.showSelection_10 = true
    },
    finishSelection_10 (index) {
      this.showSelection_10 = false
      console.log(index)
      switch (index) {
        case 0:
          this.$store.commit('addBadImpression')
          break
        case 1:
          this.$store.commit('addGoodImpression')
          break
        default:
          break
      }
      this.currruntImpression()
      if (this.$store.state.goodImpression > this.$store.state.badImpression) {
        this.showDialogue_11 = true
      } else {
        this.showDialogue_12 = true
      }
    },
    finishConversation_11 (msgs) {
      this.showDialogue_11 = false
      this.showSelection_11 = true
    },
    finishSelection_11 (index) {
      this.showSelection_11 = false
      console.log(index)
      switch (index) {
        case 0:
          this.showDialogue_11_1 = true
          break
        case 1:
          this.showDialogue_11_2 = true
          break
        default:
          break
      }
    },
    finishConversation_11_1 (msgs) {
      this.showDialogue_11_1 = false
      this.$router.push({
        path: '/commute-kim-hauntedhouse-fight'
      })
    },
    finishConversation_11_2 (msgs) {
      this.showDialogue_11_2 = false
      this.showSelection_13 = true
    },
    finishConversation_12 (msgs) {
      this.showDialogue_12 = false
      this.$router.push({
        path: '/commute-kim-hauntedhouse-fight'
      })
    },
    finishSelection_13 (index) {
      this.showSelection_13 = false
      console.log(index)
      switch (index) {
        case 0:
          this.showDialogue_13_1 = true
          break
        case 1:
          this.showDialogue_13_2 = true
          break
        default:
          break
      }
    },
    finishConversation_13_1 (msgs) {
      this.showDialogue_13_1 = false
      this.$router.push({
        path: '/commute-kim-hauntedhouse-fight'
      })
    },
    finishConversation_13_2 (msgs) {
      this.showDialogue_13_2 = false
      this.$router.push({
        path: './commute-train-journey'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/south-main-station-bg.png');
      background-size: contain;
      position: relative;
      #avator {
        @extend fullScreenImage;
        background-image: url('~@/assets/images/commute/passenger-color-center.png');
        z-index: 0;
      }
      .theme {
        position: absolute;
        bottom: 15%;
      }
    }
  }
</style>



